import React, {Component} from 'react';
import {ImageDisplayProperties} from '../../content/displayproperties/ImageDisplayProperties';
import {MultiImageDisplayProperties} from '../../content/displayproperties/MultiImageDisplayProperties';
import {Box, Stack} from '@mui/material';

export default class MultiImageCell extends Component<MultiImageDisplayProperties> {
    override render() {
        return <Stack direction={"row"} justifyContent={"space-evenly"}>
            <img className={`image-container`}
                 src={this.props.url1}
                 alt=""
                 title={this.props.description1}
            />
            <img className={`image-container`}
                 src={this.props.url2}
                 alt=""
                 title={this.props.description2}
            />
        </Stack>;
    }
}
