import {Component, ReactNode} from 'react';
import './CompletionPopup.scss';
import {
    Box,
    Button,
    Container,
    Dialog, DialogContent,
    DialogTitle,
    Divider,
    IconButton,
    Stack,
    Typography
} from '@mui/material';
import {GameContext, Guess} from '../content/GameContext';
import {ResultsSharer} from './ResultsSharer';
import {Close} from '@mui/icons-material';

export interface CompletionPopupProps<Schema> {
    show: boolean;
    onClose: () => void;
    gameContext: GameContext<Schema>;
    guesses: Guess<Schema>[];
}

export class CompletionPopup<Schema> extends Component<CompletionPopupProps<Schema>> {
    private sharer: ResultsSharer<Schema>;

    constructor(props: CompletionPopupProps<Schema>) {
        super(props);
        this.sharer = new ResultsSharer(props.gameContext);
        this.onCopyToClipboardClick = this.onCopyToClipboardClick.bind(this);
    }

    private onCopyToClipboardClick() {
        this.sharer.copyToClipboard(this.props.guesses);
    }

    private createContent() {
        const actual = this.props.gameContext.getActualResultInformation();
        // is the last guess a match
        const isSuccess = this.props.guesses[this.props.guesses.length - 1]?.isMatch() || false;

        const message1 = isSuccess ? "CONGRATULATIONS ON WINNING" : "You didn't win :(";
        const message2 = isSuccess ? `You got it in ${this.props.guesses.length} tries!` : "You almost got it";
        return <Stack
            direction="column"
            divider={<Divider orientation="horizontal" flexItem sx={{background: "primary.main"}}/>}
            spacing={2}
            alignItems={"center"}
        >
            <Box sx={{minHeight: 160, minWidth: 160}}>
                <img src={actual[1]} alt={actual[0]} title={actual[0]}/>
            </Box>
            <Stack direction="column"
                   spacing={2}>
                <Typography id="modal-modal-description" variant={"subtitle1"} align={"center"} color={"primary"}>
                    {message1}
                </Typography>
                <Typography id="modal-modal-title" variant={"h3"} align={"center"} color={"primary"}>
                    {actual[0]}
                </Typography>
                <Typography id="modal-modal-description" variant={"subtitle1"} align={"center"} color={"primary"}>
                    {message2}
                </Typography>
            </Stack>
            <Stack direction="row" paddingBottom={2}>
                <Button onClick={this.onCopyToClipboardClick}>Copy To Clipboard</Button>
            </Stack>
        </Stack>;
    }

    override render(): ReactNode {
        return <Dialog
            open={this.props.show}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
            style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}
        >
            <Container>
                <DialogTitle>
                    <IconButton aria-label="close" onClick={this.props.onClose} sx={{
                        position: 'absolute',
                        right: 4,
                        top: 4,
                        color: "primary.main"
                    }}>
                        <Close/>
                    </IconButton>
                </DialogTitle>
                <DialogContent>
                    {this.createContent()}
                </DialogContent>
            </Container>
        </Dialog>;
    }
}
