import {Component, ReactNode} from 'react';
import './SilhouettePopup.scss';
import {
    Container,
    Dialog, DialogContent,
    DialogTitle,
    IconButton
} from '@mui/material';
import {GameContext} from '../content/GameContext';
import {Close} from '@mui/icons-material';

export interface SilhouettePopupProps<Schema> {
    show: boolean;
    onClose: () => void;
    gameContext: GameContext<Schema>;
}

export class SilhouettePopup<Schema> extends Component<SilhouettePopupProps<Schema>> {

    override render(): ReactNode {
        const image = this.props.gameContext.getActualResultInformation()[1];
        return <Dialog
            open={this.props.show}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
            style={{display: 'flex', alignItems: 'center', justifyContent: 'center'}}
        >
            <Container>
                <DialogTitle>
                    <IconButton aria-label="close" onClick={this.props.onClose} sx={{
                        position: 'absolute',
                        right: 4,
                        top: 4,
                        color: "primary.main"
                    }}>
                        <Close/>
                    </IconButton>
                </DialogTitle>
                <DialogContent>
                    <img src={image} alt="?" title={"Silhouette"} className={"silhouette"}/>
                </DialogContent>
            </Container>
        </Dialog>;
    }
}
