import React, {Component} from "react";
import {TextDisplayProperties} from '../../content/displayproperties/TextDisplayProperties';
import {Typography} from '@mui/material';

export default class TextCell extends Component<TextDisplayProperties> {
    override render() {
        return <Typography className={"text-container"} fontWeight={this.props.bold ? 'bold' : 'normal'}>
            {this.props.content}
        </Typography>;
    }
}
