import {NameComparator} from '../content/comparators/NameComparator';
import {AgeComparator} from '../content/comparators/AgeComparator';
import {GameContext} from '../content/GameContext';
import {DisplayType} from '../content/DisplayType';
import {HeightComparator} from '../content/comparators/HeightComparator';
import {PositionComparator} from '../content/comparators/PositionComparator';
import {JerseyNumberComparator} from '../content/comparators/JerseyNumberComparator';
import {TeamComparator} from '../content/comparators/TeamComparator';
import positions from "./positions.json";
import options from "./players.json";
import teams from "./teams.json";
import games from "./daily_games.json";
import {HertlHelpMessage} from './HertlHelpMessage';

export interface HertlSchema {
    id: number,
    name: string,
    age: number,
    number: number,
    team: number,
    height: number,
    nationality: string,
    position: string
}

const IMAGE_URL = "https://cms.nhl.bamgrid.com/images/headshots/current/168x168/%d.png";

const START_DATE = new Date(2022, 2, 7);

export class HertlContext extends GameContext<HertlSchema> {
    constructor() {
        let schemaValues = new Map([
            ["name", {title: "Player", type: DisplayType.TEXT, comparator: new NameComparator(), columnWidth: 6}],
            ["number", {title: "#", type: DisplayType.TEXT, comparator: new JerseyNumberComparator(3), columnWidth: 1}],
            ["age", {title: "Age", type: DisplayType.TEXT, comparator: new AgeComparator(3), columnWidth: 1}],
            ["team", {title: "Team", type: DisplayType.IMAGE, comparator: new TeamComparator(teams), columnWidth: 4}],
            ["height", {
                title: "Height",
                type: DisplayType.HEIGHT,
                comparator: new HeightComparator(3),
                columnWidth: 2
            }],
            ["nationality", {
                title: "Nationality",
                type: DisplayType.IMAGE_WITH_BORDER,
                comparator: new NameComparator(),
                columnWidth: 4
            }],
            ["position", {
                title: "Position",
                type: DisplayType.TEXT,
                comparator: new PositionComparator(positions),
                columnWidth: 1
            }],
        ]);
        super(START_DATE, "id", "name", schemaValues, ["name", "team", "nationality", "age", "height", "number", "position"], "Hertl", 6, IMAGE_URL, options, games, HertlHelpMessage, "#000000");
    }

    protected override getImageDetails(value: any, key: string): [string[], string[]] {
        switch (key) {
            case "team":
                const team = teams[value as keyof typeof teams];
                return [[team["image_url"]], [team["name"]]];
            default:
                return super.getImageDetails(value, key);
        }
    }
}
