import React, {Component, ReactNode} from 'react';
import './BaseCell.scss';
import {DisplayProperties} from '../../content/displayproperties/DisplayProperties';
import {CloseDirection, ResultState} from '../../content/comparators/BaseComparator';
import {Box, Icon, TableCell} from '@mui/material';
import {ArrowDropDown, ArrowDropDownRounded, ArrowDropUp, ArrowDropUpRounded} from '@mui/icons-material';

interface BaseCellProperties {
    display: DisplayProperties;
    content: typeof Component;
}

export class BaseCell extends Component<BaseCellProperties> {
    override render(): ReactNode {
        let bgColor = "background--far";
        switch (this.props.display.state) {
            case ResultState.MATCH:
                bgColor = "background--match";
                break;
            case ResultState.CLOSE:
                bgColor = "background--close";
                break;
        }

        let topShow = this.props.display.direction == CloseDirection.UP
        let botShow = this.props.display.direction == CloseDirection.DOWN

        const Content = this.props.content;
        return <TableCell className={bgColor}
                          align={'center'}
                          sx={{paddingY: 1}}>
            <Box>
                <Icon style={{visibility: topShow ? 'visible' : 'hidden'}}>
                    <ArrowDropUp/>
                </Icon>
                <Box className={"content"}>
                    <Content {...this.props.display.detailedProperties}/>
                </Box>
                <Icon style={{visibility: botShow ? 'visible' : 'hidden'}}>
                    <ArrowDropDown/>
                </Icon>
            </Box>
        </TableCell>;
    }
}
