import React, {Component, ReactNode} from "react";
import GuessInput from "./GuessInput";
import {Box, Button, Container, IconButton, Paper, Stack, Typography} from "@mui/material";
import ResultTable from "../resulttable/ResultTable";
import {GameContext, Guess} from '../content/GameContext';
import {CompletionPopup} from './CompletionPopup';
import {instanceOf} from "prop-types";
import {withCookies, Cookies} from 'react-cookie';
import {DateUtils} from './DateUtils';
import {SilhouettePopup} from './SilhouettePopup';
import {BarChartOutlined, HelpOutline} from '@mui/icons-material';
import {HelpPopup} from './HelpPopup';

interface GameState<T> {
    guess: T | null;
    numGuess: number;
    guesses: Guess<T>[];
    finished: boolean;
    showDialog: boolean;
    showHelp: boolean;
}

interface GameProperties<T> {
    context: GameContext<T>;
    cookies: Cookies;
}

export interface GameOption<T> {
    value: T,
    label: string
}

const COOKIE_KEY = "gamestate";

class Game<T, R extends GameContext<T>> extends Component<GameProperties<T>, GameState<T>> {
    static propTypes = {
        cookies: instanceOf(Cookies).isRequired
    };

    constructor(props: GameProperties<T>) {
        super(props);
        this.state = {
            guess: null,
            numGuess: 0,
            guesses: [],
            finished: false,
            showDialog: false,
            showHelp: false
        };
    }

    override componentDidMount() {
        // loads the picks from cookies
        let cached = this.props.cookies.get(`${this.props.context.title}_${COOKIE_KEY}`) as T[];
        if (cached == null) {
            return;
        }
        const guesses = cached.map(item => this.props.context.processGuess(item));
        const isFinished = this.props.context.isFinished(guesses);
        // Loading Gamestate
        this.setState(() => (
            {
                numGuess: cached.length,
                guesses: guesses,
                guess: null,
                selectedObj: null,
                finished: isFinished,
                showDialog: isFinished,
                showHelp: false
            }
        ), () => {
            // Cache loaded
        });
    }

    handleInput = (guess: GameOption<T> | null) => {
        if (guess == null) {
            return;
        }
        this.optionSelected(guess);
    };

    private optionSelected = (guess: GameOption<T>) => {
        const guessObj = this.props.context.processGuess(guess.value);
        this.setState(prevState => {
                const newGuesses = this.state.guesses.concat(guessObj);
                const isFinished = this.props.context.isFinished(newGuesses);
                return {
                    numGuess: prevState.numGuess + 1,
                    guesses: newGuesses,
                    guess: null,
                    finished: isFinished,
                    showDialog: isFinished
                };
            }, () => {
                let data = JSON.stringify(this.state.guesses.map(guess => guess.getOption()));
                this.props.cookies.set(`${this.props.context.title}_${COOKIE_KEY}`, data, {
                    path: '/',
                    expires: DateUtils.GetMidnight()
                });
            }
        );
    };

    private primaryButtonClicked = () => {
        this.setState(() => ({
            showDialog: true
        }));
    };

    private onPrimaryDialogClosed = () => {
        this.setState(() => ({
            showDialog: false
        }));
    };

    private openHelpDialog = () => {
        this.setState(() => ({
            showHelp: true
        }));
    };

    private onHelpDialogClosed = () => {
        this.setState(() => ({
            showHelp: false
        }));
    };

    private getHeaderRow = () => {
        return <Stack sx={{backgroundColor: "primary.main", width: "100%" }}
                      direction="row" display="flex"
                      justifyContent="flex-start" alignItems="center"
                      position={"relative"}>
            <IconButton sx={{color: "secondary.main", marginLeft: "auto", flex: "0 1 auto"}}>
                <BarChartOutlined/>
            </IconButton>
            <IconButton sx={{color: "secondary.main"}} onClick={this.openHelpDialog}>
                <HelpOutline/>
            </IconButton>
        </Stack>;
    };

    override render(): ReactNode {
        const buttonText = this.state.finished ? "View Result" : "Show Silhouette";
        return <Container maxWidth={false} sx={{width: "100%", height: "100%", backgroundColor: "background.default"}} disableGutters>
            {this.getHeaderRow()}
            <Stack
                direction="column"
                spacing={2}
                alignItems={"center"}
                sx={{paddingX: 8, paddingY: 4}}>
                <Typography sx={{color: "primary.main"}}
                            fontWeight={'bold'}
                            textAlign={"center"}
                            variant={"h4"}>
                    {this.props.context.title}
                </Typography>
                <GuessInput handleInput={this.handleInput}
                            options={this.props.context.getOptions(this.state.guesses.map(guess => guess.getOption()))}
                            totalGuesses={this.props.context.totalGuesses}
                            previousGuesses={this.state.guesses}
                            finished={this.state.finished}
                />
                <Button variant="outlined" onClick={this.primaryButtonClicked}>{buttonText}</Button>
                <ResultTable columns={this.props.context.getColumnTitles()}
                             widths={this.props.context.getColumnWidths()}
                             rows={this.props.context.getGuessDisplayProperties(this.state.guesses)}
                />
                <SilhouettePopup show={this.state.showDialog && !this.state.finished} onClose={this.onPrimaryDialogClosed}
                                 gameContext={this.props.context}/>
                <CompletionPopup show={this.state.showDialog && this.state.finished} onClose={this.onPrimaryDialogClosed}
                                 gameContext={this.props.context}
                                 guesses={this.state.guesses}/>
                <HelpPopup show={this.state.showHelp} onClose={this.onHelpDialogClosed} gameContext={this.props.context}/>
            </Stack>
        </Container>;
    }
}

export default withCookies(Game);
